import React from 'react';
import * as user from '../services/data';

import './CardUser.css';

class CardUser extends React.Component {
    constructor() {
        super();
        this.createCardSocial = this.createCardSocial.bind(this);
    }

    createCardSocial( name, desc, url, key ){
        return (
            <a href={ url } key={ key } target="_blank" rel="noopener noreferrer" >
                <div className="social-media">
                    <img
                    src={ user.getImport(name)}
                    alt={ desc }
                    />
                </div>
            </a>
        );
    }

    render() {
        const { user } = this.props;
        const { firstname, lastname, occupation, social } = user;
        return (
            <section className="main-home">
                <div className="firstDot">
                    <div className="secondDot">
                        <div className="dotImage">
                        </div>
                    </div>
                </div>
                <div className="square-info">
                    <div className="infoUser">
                        <div className="nameOccupationAndContact">
                            <h1 className="userName">{`${ firstname } ${ lastname }`}</h1>
                            <h2 className="occupationUser">{ occupation }</h2>
                            <section className="contacts">
                                {social.map((item, index) => this.createCardSocial(item.name, item.description, item.url, index))}
                            </section>
                        </div>
                    </div>
                </div>
          </section>
        );
    }
}

export default CardUser;